.promo {
  background: linear-gradient(180deg, rgba(134, 36, 170, 0.1) 0%, rgba(0, 0, 0, 0) 100%),
    linear-gradient(180deg, rgba(15, 15, 20, 0) 0%, #0f0f14 100%),
    url("../img/bg-promo.png") no-repeat center top/contain, var(--bg);

  &__wrap {
  }

  &__descr {
    max-width: 658px;
    margin: 0 auto;
    text-align: center;
  }

  &__title {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 45px;
    line-height: 64px;
    text-transform: uppercase;
    margin-top: 160px;
    span {
      font-size: 80px;
    }
  }

  &__subtitle {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 40px;
  }
  &__button {
    display: flex;
    justify-content: center;
  }
  &__btn {
    margin-top: 30px;
  }

  &__cards {
    padding: 10px;
    margin-top: 200px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 250px);
    gap: 25px;
    overflow: hidden;
    justify-content: space-between;
    &_hidden {
      max-height: 700px;
      position: relative;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 50%;
        width: 100%;
        background: linear-gradient(180deg, rgba(134, 36, 170, 0.1) 0%, rgba(0, 0, 0, 0) 100%),
          linear-gradient(180deg, rgba(15, 15, 20, 0) 0%, #0f0f14 100%);
      }
    }
  }
}

.card {
  &__item {
    position: relative;
    transition: all 0.3s ease 0s;
    border-radius: 5px;
    &:hover {
      transform: scale(1.05);
    }
    &:hover .card__overlay {
      opacity: 1;
      visibility: visible;
    }
    cursor: pointer;
    img {
      border-radius: 5px;
      transition: all 0.3s ease 0s;
      min-height: 100%;
    }
  }
  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease 0s;
    background: linear-gradient(0deg, rgba(15, 15, 20, 0.2), rgba(15, 15, 20, 0.2)),
      linear-gradient(180deg, rgba(134, 36, 170, 0) 0%, rgba(134, 36, 170, 0.8) 100%);
    opacity: 0;
    visibility: hidden;
    border-radius: 5px;
  }

  &__btn {
    padding-left: 74px;
    padding-right: 74px;
    margin-top: -40px;
    position: relative;
    z-index: 5;
    &_hidden {
      display: none;
    }
  }
}
