.footer {
  background-color: var(--colorBlack);
  padding-top: 50px;
  padding-bottom: 32px;
  &__wrap {
  }

  &__top {
    position: relative;
  }

  &__bottom {
  }
}

.top-footer {
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 50px;
    flex-wrap: wrap;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 1px;
      background-color: var(--colorWhite);
      opacity: 0.1;
    }
  }
  .flex-bottom {
    align-self: flex-end;
  }
  &__item {
  }

  &__title {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    color: var(--colorWhite);
    margin-bottom: 10px;
  }

  &__list {
    li {
      a {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 25px;
        color: var(--colorWhite);
      }
    }
  }
}
.bottom-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 27px;
  &__copyright {
    display: flex;
    align-items: center;
  }

  &__logo {
  }

  &__text {
    margin-left: 30px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 25px;
    color: var(--colorGray);
    max-width: 650px;
  }

  &__social {
  }

  &__icons {
    display: flex;
  }

  &__icon {
    margin-left: 10px;
    a {
      color: var(--colorBlack);
      width: 40px;
      height: 40px;
      background-color: var(--colorWhite);
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 19px;
      transition: all 0.3s ease 0s;
      &:hover {
        box-shadow: 1px 1px 3px rgba(255, 255, 255, 0.5), -1px -1px 3px rgba(255, 255, 255, 0.5);
      }
    }
  }
}
