.testimonials {
  padding-top: 80px;
  padding-bottom: 100px;
  background: url("../img/bg-testimonials.png") no-repeat center top/contain, var(--bg);
  &__wrap {
  }

  &__title {
    text-align: center;
  }

  &__subtitle {
    text-align: center;
    margin-top: 10px;
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    color: var(--colorYellow);
    span {
      color: var(--colorGray);
      margin-left: 30px;
    }
  }

  &__slider {
    margin-top: 40px;
  }
}
.slider-testimonials {
  &__slide {
    background-color: var(--colorBlack);
    padding: 30px;
    border-radius: 5px;
  }

  &__raiting {
    color: var(--colorYellow);
  }

  &__title {
    margin-top: 20px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--colorWhite);
  }

  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
    margin-top: 10px;
  }

  &__author {
    display: flex;
    margin-top: 20px;
    align-items: center;
  }

  &__avatar {
  }

  &__data {
    padding-left: 15px;
  }

  &__name {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--colorWhite);
  }

  &__date {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
  }
}

.swiper-pagination-bullet {
  width: 30px;
  height: 5px;
  background: #8624aa;
  opacity: 0.5;
  border-radius: 0px;
  &-active {
    width: 60px;
    background: #8624aa;
    opacity: 1;
  }
}

.swiper-slide {
  box-sizing: border-box;
  width: 430px !important;
  height: 280px !important;
}
.advantages {
  &__rait {
    margin-top: 65px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
.rait {
  &__item {
    display: flex;
    align-items: center;
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--colorWhite);
    margin-right: 30px;
    margin-top: 15px;
    &:last-child {
      margin-right: 0;
    }
    span {
      font-weight: 600;
      font-size: 30px;
    }
  }

  &__img {
  }

  &__text {
    padding-left: 30px;
  }
}
