.header {
  padding-top: 40px;
  padding-bottom: 40px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__btn {
    margin-left: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      width: 18px;
      height: 18px;
      margin-right: 9px;
      fill: #fff;
    }
  }
  .burger {
    position: relative;
    z-index: 99;
    display: block;
    width: 40px;
    height: 20px;
    cursor: pointer;
    background-color: transparent;
    position: relative;
    display: none;
    transition: all 0.3s ease 0s;
    &:after,
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0px;
      width: 100%;
      height: 2px;
      background-color: var(--colorWhite);
      transition: all 0.3s ease 0s;
    }
    &:before {
      top: auto;
      bottom: 0px;
    }
    span {
      position: absolute;
      width: 100%;
      height: 2px;
      display: inline-block;
      background-color: var(--colorWhite);
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all 0.3s ease 0s;
    }
    &.open {
      &:after {
        transform: rotate(-45deg);
        top: 9px;
      }
      &:before {
        transform: rotate(45deg);
        bottom: 9px;
      }
    }
    &.open {
      span {
        transform: scale(0);
      }
    }
  }
}

.nav {
  display: flex;
  &__cart {
    img {
      width: 25px;
      height: 25px;
    }
  }
  &__log {
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
      img {
        margin-right: 9px;
      }
    }
  }

  &__cart {
    cursor: pointer;
    margin-left: 60px;
    svg {
      fill: #fff;
      width: 30px;
      height: 30px;
    }
  }
}
.menu {
  display: flex;
  align-items: center;
  &__item {
    margin-left: 40px;
  }

  &__link {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--colorWhite);
    position: relative;
    &:after {
      content: "";
      background: var(--colorWhite);
      position: absolute;
      left: 0;
      bottom: 0px;
      width: 100%;
      height: 2px;
      transform: scale(0);
      transition: all 0.5s ease 0s;
    }
    &:hover:after {
      transform: scale(1);
    }
  }
}
