.about {
  background: var(--bg);
  padding-top: 230px;
  padding-bottom: 230px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__img {
    flex: 1 0 50%;
  }

  &__descr {
  }

  &__title {
  }

  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
  }

  &__btn {
  }
}
.btn {
  &__outline {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    color: var(--colorWhite);
    background: var(--bg);
    border-radius: 25px;
    border: 2px solid var(--colorPink);
    padding: 13px 46px;
    cursor: pointer;
    margin-top: 24px;
  }
}
