// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base

  --font-family: "Open Sans", sans-serif;
  --content-width: 1385px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  //colors

  --colorWhite: #ffffff;
  --colorPink: #8624aa;
  --colorGray: #cccccc;
  --colorYellow: #ffa800;
  --colorBlack: #16161e;
  --bg: #0f0f14;
}
