.fast {
  background-color: var(--bg);
  &__wrap {
  }

  &__title {
    text-align: center;
  }

  &__descr {
    text-align: center;
    margin-top: 20px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
  }

  &__items {
    margin-top: 65px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 30px;
  }

  &__item {
    flex-basis: 22%;
    text-align: center;
    height: 270px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      top: 40px;
      right: -44px;
      background: url("../img/fast-arrow.svg") no-repeat;
      width: 40px;
      height: 12px;
    }
    &:last-child {
      &:after {
        background: none;
      }
    }
  }
}

.item-fast {
  &__img {
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title {
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    margin-top: 10px;
  }

  &__descr {
    margin-top: 10px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
  }
}
