@media (max-width: 1200px) {
  .about {
    padding-top: 120px;
    padding-bottom: 120px;
    &__descr {
      padding-left: 15px;
    }
    &__text {
      font-size: 14px;
    }
    .btn {
      &__outline {
        margin-top: 5px;
      }
    }
    &__title {
      font-size: 30px;
    }
  }
  .fast {
    &__title {
      font-size: 30px;
    }
    &__descr {
      font-size: 14px;
    }
    &__items {
      margin-top: 35px;
      justify-content: center;
    }
    &__item {
      flex-basis: 30%;
      &:nth-child(3) {
        &:after {
          background: none;
        }
      }
    }
    .item-fast {
      &__title {
        font-size: 14px;
      }
      &__descr {
        font-size: 14px;
      }
    }
  }
  .advantages {
    padding-top: 80px;
    padding-bottom: 100px;
    &__descr {
      flex-basis: 70%;
    }
    &__title {
      font-size: 30px;
    }
    .item-advant {
      &__title {
        font-size: 17px;
      }
      &__text {
        font-size: 14px;
      }
    }
  }
  .testimonials {
    &__title {
      font-size: 30px;
    }
    &__subtitle {
      font-size: 17px;
      line-height: 22px;
    }
    .slider-testimonials {
      &__title {
        font-size: 15px;
      }
      &__text {
        font-size: 14px;
      }
      &__name {
        font-size: 15px;
      }
      &__date {
        font-size: 14px;
      }
    }
  }
  .advantages {
    &__rait {
      margin-top: 65px;
      display: flex;
      justify-content: center;
    }
  }
  .faq {
    padding-top: 60px;
    &__title {
      font-size: 30px;
    }
    .tabs {
      margin-top: 20px;
      &__item {
        font-size: 21px;
      }
    }
  }
}
@media (max-width: 992px) {
  .header {
    .burger {
      display: block;
      z-index: 201;
    }
    .menu {
      position: absolute;
      top: 0;
      left: 0;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      z-index: 200;
      text-align: center;
      background-color: rgba(0, 0, 0, 0.76);
      justify-content: center;
      transform: translateY(-120%);
      transition: all 0.5s ease 0s;
      &.transformation {
        transform: translateY(0%);
      }
      &__link {
        font-size: 25px;
        &:hover:after {
          transform: scale(0);
        }
      }
      &__item {
        margin-left: 0;
        margin-bottom: 15px;
      }
    }
    .nav {
      &__cart {
        img {
          margin-top: 4px;
        }
      }
    }
  }
  .promo {
    &__title {
      font-size: 35px;
      line-height: 50px;
      margin-top: 80px;
      span {
        font-size: 65px;
      }
    }
    &__subtitle {
      font-size: 18px;
    }
    &__cards {
      margin-top: 80px;
      justify-content: center;
    }
  }
  .advantages {
    &__wrap {
      flex-direction: column;
    }
    &__title {
      text-align: center;
    }
    &__items {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  .faq {
    padding-top: 20px;
    padding-bottom: 150px;
  }
  .footer {
    .bottom-footer {
      &__text {
        font-size: 12px;
      }
      &__icon {
        a {
          width: 30px;
          height: 30px;
          font-size: 16px;
        }
      }
    }
  }
  .chat {
    bottom: 60px;
    img {
      width: 51px;
      height: 60px;
    }
  }
}

@media (max-width: 768px) {
  .header {
    .nav {
      &__cart {
        margin-left: 10px;
        svg {
          width: 25px;
          height: 25px;
        }
      }
    }
    &__btn {
      margin-left: 20px;
      padding: 5px 27px;
      font-size: 12px;
      line-height: 20px;
      svg {
        width: 14px;
        height: 14px;
      }
    }
  }
  .promo {
    &__title {
      font-size: 30px;
      line-height: 45px;
      margin-top: 55px;
      span {
        font-size: 55px;
      }
    }
    &__subtitle {
      font-size: 16px;
    }
    &__cards {
      margin-top: 60px;
      justify-content: center;
    }
  }
  .about {
    &__wrap {
      display: block;
    }
  }
  .fast {
    &__item {
      flex-basis: 45%;
      &:after {
        background: none;
      }
    }
  }
  .testimonials {
    padding-top: 40px;
    padding-bottom: 40px;
  }
  .faq {
    padding-bottom: 100px;
    .tabs {
      &__item {
        font-size: 15px;
      }
    }
    .accordion {
      &__control {
        font-size: 15px;
        line-height: 20px;
      }
      &__content {
        font-size: 13px;
        line-height: 17px;
        &.open {
          padding-top: 0px;
        }
      }
      &__icon {
        width: 8px;
        height: 8px;
      }
    }
  }
  .footer {
    .top-footer {
      &__list {
        li {
          a {
            font-size: 13px;
          }
        }
      }
    }
    .bottom-footer {
      &__text {
        line-height: 16px;
        margin-left: 15px;
      }
      &__icon {
        a {
          width: 30px;
          height: 30px;
          font-size: 16px;
        }
      }
    }
  }
}

@media (max-width: 576px) {
  .header {
    padding-top: 15px;
    padding-bottom: 15px;
    &__btn {
      padding: 10px;
      border-radius: 100%;
      font-size: 0;
      svg {
        height: 18px;
        width: 18px;
        margin-right: 0;
      }
    }
    .nav {
      &__cart {
        svg {
          width: 20px;
          height: 20px;
        }
        img {
          margin-top: 5px;
        }
      }
      &__log {
        a {
          img {
            margin-right: 0px;
          }
        }
      }
    }
    .burger {
      width: 35px;
    }
    &__logo {
      max-width: 40%;
    }
  }
  .promo {
    &__title {
      font-size: 20px;
      line-height: 35px;
      margin-top: 50px;
      span {
        font-size: 36px;
      }
    }
    &__subtitle {
      font-size: 14px;
    }
    &__btn {
      margin-top: 25px;
      padding: 8px 25px;
    }
    &__cards {
      margin-top: 80px;
      justify-content: center;
    }
  }
  .about {
    padding-top: 80px;
    padding-bottom: 80px;
    &__descr {
      padding-left: 0;
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      font-size: 13px;
    }
    .btn {
      &__outline {
        font-size: 14px;
        padding: 5px 35px;
      }
    }
  }
  .fast {
    &__title {
      font-size: 24px;
      line-height: 35px;
    }
    &__descr {
      font-size: 13px;
    }
    &__item {
      flex-basis: 100%;
      height: 230px;
    }
  }
  .advantages {
    padding-top: 40px;
    padding-bottom: 40px;
    &__img {
      order: -1;
      margin-bottom: 30px;
    }
    &__title {
      font-size: 24px;
      line-height: 35px;
    }
    &__item {
      flex-direction: column;
      text-align: center;
    }
    .item-advant {
      &__descr {
        padding-left: 0;
      }
      &__title {
        margin-top: 10px;
      }
    }
  }
  .testimonials {
    .swiper-slide {
      width: 80% !important;
      height: auto !important;
    }
    &__title {
      font-size: 24px;
      line-height: 35px;
    }
    &__subtitle {
      font-size: 14px;
      line-height: 19px;
    }
    .slider-testimonials {
      &__title {
        font-size: 14px;
      }
      &__text {
        font-size: 13px;
        line-height: 19px;
      }
      &__name {
        font-size: 14px;
      }
      &__date {
        font-size: 13px;
        line-height: 13px;
      }
      &__avatar {
        img {
          max-width: 85%;
        }
      }
    }
  }
  .faq {
    &__title {
      font-size: 24px;
      line-height: 35px;
    }
    .tabs {
      &__header {
        flex-direction: column;
      }
      &__item {
        margin-bottom: 10px;
      }
    }
  }
  .footer {
    .top-footer {
      &__item {
        flex-basis: 50%;
      }
    }
    .bottom-footer {
      flex-direction: column;
      &__copyright {
        margin-bottom: 20px;
      }
    }
  }
  .chat {
    bottom: 60px;
    right: 20px;
    img {
      width: 43px;
      height: 50px;
    }
  }
}

@media (max-width: 400px) {
  .footer {
    .top-footer {
      &__item {
        margin-bottom: 15px;
        flex-basis: 100%;
      }
    }
  }
}

@media (max-width: 370px) {
  .testimonials {
    .advantages {
      &__rait {
        justify-content: space-between;
      }
    }
  }
  .faq {
    .accordion {
      &__control {
        padding-left: 15px;
        padding-right: 15px;
        font-size: 14px;
      }
      &__title {
        display: block;
        max-width: 90%;
        text-align: left;
      }
      &__content {
        &.open {
          padding-bottom: 50px;
          padding-left: 15px;
        }
      }
    }
  }
}
