.faq {
  padding-top: 100px;
  padding-bottom: 200px;
  background: var(--bg);
  &__wrap {
  }

  &__title {
    text-align: center;
  }

  &__tabs {
  }
}

.tabs {
  max-width: 1120px;
  margin: 30px auto 0 auto;
  &__header {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__item {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    color: #aaaaaa;
    cursor: pointer;
    margin: 0 25px;
    &.active {
      color: var(--colorWhite);
      position: relative;
      &:after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -3px;
        width: 100%;
        height: 2px;
        background: var(--colorPink);
      }
    }
  }

  &__body {
    margin-top: 40px;
  }

  &__info {
    display: none;
    &.active {
      display: block;
    }
  }
}

.accordion {
  &__list {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__control {
    background: var(--colorBlack);
    width: 100%;
    text-align-last: left;
    padding: 15px 30px;
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: var(--colorWhite);
    cursor: pointer;
    border-radius: 5px 5px 0 0;
    position: relative;
  }
  &__icon {
    position: absolute;
    width: 10px;
    height: 10px;
    border-left: 2px solid var(--colorWhite);
    border-bottom: 2px solid var(--colorWhite);
    background-color: transparent;
    right: 20px;
    top: 50%;
    transform: translateY(-50%) rotate(-45deg);
    transition: all 0.3s ease 0s;
    &.open {
      transform: rotate(135deg);
    }
  }
  &__title {
  }

  &__content {
    background: var(--colorBlack);
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: var(--colorGray);
    position: relative;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    max-height: 0;
    box-sizing: content-box;
    transition: all 0.3s ease-out 0s;
    &.open {
      opacity: 1;
      visibility: visible;
      padding: 15px 30px 40px 30px;
    }

    p {
      margin: 0;
    }
  }
}
