:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1385px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --colorWhite: #ffffff;
  --colorPink: #8624aa;
  --colorGray: #cccccc;
  --colorYellow: #ffa800;
  --colorBlack: #16161e;
  --bg: #0f0f14;
}

/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  color: var(--colorWhite);
}
.page__body.lock {
  overflow: hidden;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

button {
  border: none;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  padding: 9px 40px;
  border-radius: 25px;
  background-color: var(--colorPink);
  color: var(--colorWhite);
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
.btn:hover {
  box-shadow: 2px 2px 5px rgba(218, 154, 241, 0.2), -2px -2px 5px rgba(218, 154, 241, 0.2);
}

.title {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  line-height: 50px;
  text-transform: uppercase;
}

.chat {
  position: fixed;
  right: 40px;
  bottom: 60px;
}
.chat:hover {
  animation: chat 0.2s ease 0s infinite alternate;
}

@keyframes chat {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-7px);
  }
}
.header {
  padding-top: 40px;
  padding-bottom: 40px;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btn {
  margin-left: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__btn svg {
  width: 18px;
  height: 18px;
  margin-right: 9px;
  fill: #fff;
}
.header .burger {
  position: relative;
  z-index: 99;
  display: block;
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: transparent;
  position: relative;
  display: none;
  transition: all 0.3s ease 0s;
}
.header .burger:after, .header .burger:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0px;
  width: 100%;
  height: 2px;
  background-color: var(--colorWhite);
  transition: all 0.3s ease 0s;
}
.header .burger:before {
  top: auto;
  bottom: 0px;
}
.header .burger span {
  position: absolute;
  width: 100%;
  height: 2px;
  display: inline-block;
  background-color: var(--colorWhite);
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease 0s;
}
.header .burger.open:after {
  transform: rotate(-45deg);
  top: 9px;
}
.header .burger.open:before {
  transform: rotate(45deg);
  bottom: 9px;
}
.header .burger.open span {
  transform: scale(0);
}

.nav {
  display: flex;
}
.nav__cart img {
  width: 25px;
  height: 25px;
}
.nav__log {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__log a img {
  margin-right: 9px;
}
.nav__cart {
  cursor: pointer;
  margin-left: 60px;
}
.nav__cart svg {
  fill: #fff;
  width: 30px;
  height: 30px;
}

.menu {
  display: flex;
  align-items: center;
}
.menu__item {
  margin-left: 40px;
}
.menu__link {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
  text-transform: uppercase;
  color: var(--colorWhite);
  position: relative;
}
.menu__link:after {
  content: "";
  background: var(--colorWhite);
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 100%;
  height: 2px;
  transform: scale(0);
  transition: all 0.5s ease 0s;
}
.menu__link:hover:after {
  transform: scale(1);
}

.promo {
  background: linear-gradient(180deg, rgba(134, 36, 170, 0.1) 0%, rgba(0, 0, 0, 0) 100%), linear-gradient(180deg, rgba(15, 15, 20, 0) 0%, #0f0f14 100%), url("../img/bg-promo.png") no-repeat center top/contain, var(--bg);
}
.promo__descr {
  max-width: 658px;
  margin: 0 auto;
  text-align: center;
}
.promo__title {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 64px;
  text-transform: uppercase;
  margin-top: 160px;
}
.promo__title span {
  font-size: 80px;
}
.promo__subtitle {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 40px;
}
.promo__button {
  display: flex;
  justify-content: center;
}
.promo__btn {
  margin-top: 30px;
}
.promo__cards {
  padding: 10px;
  margin-top: 200px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 250px);
  gap: 25px;
  overflow: hidden;
  justify-content: space-between;
}
.promo__cards_hidden {
  max-height: 700px;
  position: relative;
}
.promo__cards_hidden:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 50%;
  width: 100%;
  background: linear-gradient(180deg, rgba(134, 36, 170, 0.1) 0%, rgba(0, 0, 0, 0) 100%), linear-gradient(180deg, rgba(15, 15, 20, 0) 0%, #0f0f14 100%);
}

.card__item {
  position: relative;
  transition: all 0.3s ease 0s;
  border-radius: 5px;
  cursor: pointer;
}
.card__item:hover {
  transform: scale(1.05);
}
.card__item:hover .card__overlay {
  opacity: 1;
  visibility: visible;
}
.card__item img {
  border-radius: 5px;
  transition: all 0.3s ease 0s;
  min-height: 100%;
}
.card__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease 0s;
  background: linear-gradient(0deg, rgba(15, 15, 20, 0.2), rgba(15, 15, 20, 0.2)), linear-gradient(180deg, rgba(134, 36, 170, 0) 0%, rgba(134, 36, 170, 0.8) 100%);
  opacity: 0;
  visibility: hidden;
  border-radius: 5px;
}
.card__btn {
  padding-left: 74px;
  padding-right: 74px;
  margin-top: -40px;
  position: relative;
  z-index: 5;
}
.card__btn_hidden {
  display: none;
}

.about {
  background: var(--bg);
  padding-top: 230px;
  padding-bottom: 230px;
}
.about__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about__img {
  flex: 1 0 50%;
}
.about__text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
}
.btn__outline {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--colorWhite);
  background: var(--bg);
  border-radius: 25px;
  border: 2px solid var(--colorPink);
  padding: 13px 46px;
  cursor: pointer;
  margin-top: 24px;
}

.fast {
  background-color: var(--bg);
}
.fast__title {
  text-align: center;
}
.fast__descr {
  text-align: center;
  margin-top: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
}
.fast__items {
  margin-top: 65px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  gap: 30px;
}
.fast__item {
  flex-basis: 22%;
  text-align: center;
  height: 270px;
  position: relative;
}
.fast__item:after {
  content: "";
  position: absolute;
  top: 40px;
  right: -44px;
  background: url("../img/fast-arrow.svg") no-repeat;
  width: 40px;
  height: 12px;
}
.fast__item:last-child:after {
  background: none;
}

.item-fast__img {
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.item-fast__title {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin-top: 10px;
}
.item-fast__descr {
  margin-top: 10px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
}

.advantages {
  background: var(--bg);
  padding-top: 150px;
  padding-bottom: 215px;
}
.advantages__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.advantages__descr {
  flex: 0 1 50%;
}
.advantages__items {
  margin-top: 20px;
  max-width: 650px;
}
.advantages__item {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.advantages__item:last-child {
  margin-bottom: 0;
}
.item-advant__descr {
  padding-left: 30px;
}
.item-advant__title {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
  color: var(--colorWhite);
}
.item-advant__text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
  margin-top: 5px;
}

.testimonials {
  padding-top: 80px;
  padding-bottom: 100px;
  background: url("../img/bg-testimonials.png") no-repeat center top/contain, var(--bg);
}
.testimonials__title {
  text-align: center;
}
.testimonials__subtitle {
  text-align: center;
  margin-top: 10px;
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--colorYellow);
}
.testimonials__subtitle span {
  color: var(--colorGray);
  margin-left: 30px;
}
.testimonials__slider {
  margin-top: 40px;
}

.slider-testimonials__slide {
  background-color: var(--colorBlack);
  padding: 30px;
  border-radius: 5px;
}
.slider-testimonials__raiting {
  color: var(--colorYellow);
}
.slider-testimonials__title {
  margin-top: 20px;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: var(--colorWhite);
}
.slider-testimonials__text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
  margin-top: 10px;
}
.slider-testimonials__author {
  display: flex;
  margin-top: 20px;
  align-items: center;
}
.slider-testimonials__data {
  padding-left: 15px;
}
.slider-testimonials__name {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: var(--colorWhite);
}
.slider-testimonials__date {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: var(--colorGray);
}

.swiper-pagination-bullet {
  width: 30px;
  height: 5px;
  background: #8624aa;
  opacity: 0.5;
  border-radius: 0px;
}
.swiper-pagination-bullet-active {
  width: 60px;
  background: #8624aa;
  opacity: 1;
}

.swiper-slide {
  box-sizing: border-box;
  width: 430px !important;
  height: 280px !important;
}

.advantages__rait {
  margin-top: 65px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.rait__item {
  display: flex;
  align-items: center;
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: var(--colorWhite);
  margin-right: 30px;
  margin-top: 15px;
}
.rait__item:last-child {
  margin-right: 0;
}
.rait__item span {
  font-weight: 600;
  font-size: 30px;
}
.rait__text {
  padding-left: 30px;
}

.faq {
  padding-top: 100px;
  padding-bottom: 200px;
  background: var(--bg);
}
.faq__title {
  text-align: center;
}
.tabs {
  max-width: 1120px;
  margin: 30px auto 0 auto;
}
.tabs__header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs__item {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #aaaaaa;
  cursor: pointer;
  margin: 0 25px;
}
.tabs__item.active {
  color: var(--colorWhite);
  position: relative;
}
.tabs__item.active:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -3px;
  width: 100%;
  height: 2px;
  background: var(--colorPink);
}
.tabs__body {
  margin-top: 40px;
}
.tabs__info {
  display: none;
}
.tabs__info.active {
  display: block;
}

.accordion__list {
  margin-bottom: 10px;
}
.accordion__list:last-child {
  margin-bottom: 0;
}
.accordion__control {
  background: var(--colorBlack);
  width: 100%;
  text-align-last: left;
  padding: 15px 30px;
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: var(--colorWhite);
  cursor: pointer;
  border-radius: 5px 5px 0 0;
  position: relative;
}
.accordion__icon {
  position: absolute;
  width: 10px;
  height: 10px;
  border-left: 2px solid var(--colorWhite);
  border-bottom: 2px solid var(--colorWhite);
  background-color: transparent;
  right: 20px;
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  transition: all 0.3s ease 0s;
}
.accordion__icon.open {
  transform: rotate(135deg);
}
.accordion__content {
  background: var(--colorBlack);
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: var(--colorGray);
  position: relative;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  max-height: 0;
  box-sizing: content-box;
  transition: all 0.3s ease-out 0s;
}
.accordion__content.open {
  opacity: 1;
  visibility: visible;
  padding: 15px 30px 40px 30px;
}
.accordion__content p {
  margin: 0;
}

.footer {
  background-color: var(--colorBlack);
  padding-top: 50px;
  padding-bottom: 32px;
}
.footer__top {
  position: relative;
}
.top-footer__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 50px;
  flex-wrap: wrap;
}
.top-footer__items:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: var(--colorWhite);
  opacity: 0.1;
}
.top-footer .flex-bottom {
  align-self: flex-end;
}
.top-footer__title {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 30px;
  color: var(--colorWhite);
  margin-bottom: 10px;
}
.top-footer__list li a {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;
  color: var(--colorWhite);
}

.bottom-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 27px;
}
.bottom-footer__copyright {
  display: flex;
  align-items: center;
}
.bottom-footer__text {
  margin-left: 30px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;
  color: var(--colorGray);
  max-width: 650px;
}
.bottom-footer__icons {
  display: flex;
}
.bottom-footer__icon {
  margin-left: 10px;
}
.bottom-footer__icon a {
  color: var(--colorBlack);
  width: 40px;
  height: 40px;
  background-color: var(--colorWhite);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  transition: all 0.3s ease 0s;
}
.bottom-footer__icon a:hover {
  box-shadow: 1px 1px 3px rgba(255, 255, 255, 0.5), -1px -1px 3px rgba(255, 255, 255, 0.5);
}

@media (max-width: 1200px) {
  .about {
    padding-top: 120px;
    padding-bottom: 120px;
  }
  .about__descr {
    padding-left: 15px;
  }
  .about__text {
    font-size: 14px;
  }
  .about .btn__outline {
    margin-top: 5px;
  }
  .about__title {
    font-size: 30px;
  }

  .fast__title {
    font-size: 30px;
  }
  .fast__descr {
    font-size: 14px;
  }
  .fast__items {
    margin-top: 35px;
    justify-content: center;
  }
  .fast__item {
    flex-basis: 30%;
  }
  .fast__item:nth-child(3):after {
    background: none;
  }
  .fast .item-fast__title {
    font-size: 14px;
  }
  .fast .item-fast__descr {
    font-size: 14px;
  }

  .advantages {
    padding-top: 80px;
    padding-bottom: 100px;
  }
  .advantages__descr {
    flex-basis: 70%;
  }
  .advantages__title {
    font-size: 30px;
  }
  .advantages .item-advant__title {
    font-size: 17px;
  }
  .advantages .item-advant__text {
    font-size: 14px;
  }

  .testimonials__title {
    font-size: 30px;
  }
  .testimonials__subtitle {
    font-size: 17px;
    line-height: 22px;
  }
  .testimonials .slider-testimonials__title {
    font-size: 15px;
  }
  .testimonials .slider-testimonials__text {
    font-size: 14px;
  }
  .testimonials .slider-testimonials__name {
    font-size: 15px;
  }
  .testimonials .slider-testimonials__date {
    font-size: 14px;
  }

  .advantages__rait {
    margin-top: 65px;
    display: flex;
    justify-content: center;
  }

  .faq {
    padding-top: 60px;
  }
  .faq__title {
    font-size: 30px;
  }
  .faq .tabs {
    margin-top: 20px;
  }
  .faq .tabs__item {
    font-size: 21px;
  }
}
@media (max-width: 992px) {
  .header .burger {
    display: block;
    z-index: 201;
  }
  .header .menu {
    position: absolute;
    top: 0;
    left: 0;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    z-index: 200;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.76);
    justify-content: center;
    transform: translateY(-120%);
    transition: all 0.5s ease 0s;
  }
  .header .menu.transformation {
    transform: translateY(0%);
  }
  .header .menu__link {
    font-size: 25px;
  }
  .header .menu__link:hover:after {
    transform: scale(0);
  }
  .header .menu__item {
    margin-left: 0;
    margin-bottom: 15px;
  }
  .header .nav__cart img {
    margin-top: 4px;
  }

  .promo__title {
    font-size: 35px;
    line-height: 50px;
    margin-top: 80px;
  }
  .promo__title span {
    font-size: 65px;
  }
  .promo__subtitle {
    font-size: 18px;
  }
  .promo__cards {
