html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  color: var(--colorWhite);
  &.lock {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

button {
  border: none;
}
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.btn {
  padding: 9px 40px;
  border-radius: 25px;
  background-color: var(--colorPink);
  color: var(--colorWhite);
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  &:hover {
    box-shadow: 2px 2px 5px rgba(#da9af1, 0.2), -2px -2px 5px rgba(#da9af1, 0.2);
  }
}
.title {
  font-family: "Kanit", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 36px;
  line-height: 50px;
  text-transform: uppercase;
}
.chat {
  position: fixed;
  right: 40px;
  bottom: 60px;
  &:hover {
    animation: chat 0.2s ease 0s infinite alternate;
  }
}
@keyframes chat {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-7px);
  }
}
