.advantages {
  background: var(--bg);
  padding-top: 150px;
  padding-bottom: 215px;

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__descr {
    flex: 0 1 50%;
  }

  &__items {
    margin-top: 20px;
    max-width: 650px;
  }

  &__item {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__img {
  }
}

.item-advant {
  &__img {
  }

  &__descr {
    padding-left: 30px;
  }

  &__title {
    font-family: "Kanit", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    line-height: 25px;
    text-transform: uppercase;
    color: var(--colorWhite);
  }

  &__text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 25px;
    color: var(--colorGray);
    margin-top: 5px;
  }
}
